<template>
  <div class="share-container">
    <HeadTop :Headtype="Headtype" :title="$t('share.邀请好友')"></HeadTop>
    <div class="p15">
      <div class="share-top">
        <div class="address-tit">
          {{$t('share.钱包地址')}}
        </div>
        <div class="address">
          {{address}}
        </div>
        <div class="qrcode-box">
          <img :src="imgQrcode" alt="">
        </div>
        <div class="flex-column link-text">
          <div class="label">{{$t('share.邀请链接')}}
            <img class="lab-img" src="@/assets/images/share-tit.png">
          </div>
          <div class="cont flex-item">{{link}}</div>
        </div>
        <div class="share-btn">
          <van-button type="primary" class="clip-button" :data-clipboard-text="link">{{$t('share.复制邀请链接')}}</van-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import VueQr from 'vue-qr'
import HeadTop from '@/components/HeadTop' 
import { Toast } from 'vant';
import QRCode from 'qrcode'
import Clipboard from 'clipboard'
import { reactive, toRefs, computed, watch} from "vue";
import {useStore} from "vuex";
import { useI18n } from "vue-i18n";
export default {
  beforeRouteEnter (to, from, next) {
    let appbg =  document.querySelector('html')
    appbg.style.background = '#0355E1'
    next()
  },
  beforeRouteLeave (to, from, next) {
    document.querySelector('html').removeAttribute('style')
    next()
  },
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      Headtype: 'back',
      records: [],
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      freezeContract: null,
      windTokenContract: null,
      imgQrcode: '',
      reward: '',
      link: '',
      curBlock: '',
      initContract: () => {
        data.initData()
      },
      initData: () => {
        data.link = `${window.location.origin}${window.location.pathname}#/?intro=${data.address}`
        QRCode.toDataURL(data.link, {margin: 0.5, width: 163})
      .then(url => {
        data.imgQrcode = url
      })
      .catch(err => {
        console.error(err)
      })
      var clipboard = new Clipboard('.clip-button')
      clipboard.on('success', function (e) {
        Toast(t('share.复制成功'))
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast(t('share.复制失败，请手动长按复制'))
      }) 
      }
    })
     if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
  .share-container{
    .footer{
      display: none;
    }
  }
</style>
<style lang="less" scoped>
.share-container {
  background: #0355E1 url(~@/assets/images/share-bg.png) no-repeat;
  background-size: 100% 100%;
}
.share-top {
  background: url(~@/assets/images/code-bg.png) no-repeat;
  background-size: 100% auto;
  padding: 15px 15px 34px;
  border-radius: 10px;
  text-align: center;
  margin: 0 10px;
  .address-tit{
    width: 115px;
    height: 29px;
    line-height: 29px;
    background: url(~@/assets/images/wallettit-bg.png) no-repeat;
    background-size: 100% 100%;
    margin: 25px auto 0;
    color: #fff;
  }
  .address {
    padding: 10px 47px;
    word-wrap: break-word;
    color: #010101;
    font-size: 15px;
    font-weight: bold;
  }
  .qrcode-box {
    width: 184px;
    height: 184px;
    margin: 10px auto 20px;
    background: linear-gradient(221deg, rgba(31, 117, 249, .5), rgba(62, 151, 251, .5));
    padding: 10px;
  }
  .link-text {
    font-size: 15px;
    .label {
      font-size: 16px;
      font-weight: bold;
      color: #0066EE;
      position: relative;
      padding: 0 39px;
      .lab-img{
        position: absolute;
        left: 0;
        right: 0;
        margin: 0 auto;
        width: 139px;
        height: 9px;
        top: 5px;
      }
    }
    .cont {
      margin: 15px 16px 25px;
      text-align: center;
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
      font-size: 14px;
      color: #222222;
      line-height: 20px;
      padding: 15px 26px;
      width: 290px;
      background: rgba(0, 120, 238, .1);
      border-radius: 8px;
    }
    .copy-icon {
      width: 13px;
      height: 13px;
      margin: 5px 0 0 14px
    }
  }
  .share-btn{
    .van-button--primary{
      width: 65% !important;
      border-radius: 22px !important;
      background: #0066EE;
    }
  }
}
</style>
